import React, {useState, useEffect} from 'react';
import './App.css';
import Header  from './Components/HeadersComponent/Header';
import About from './Components/BodyComponents/About';
import Portfolio from './Components/BodyComponents/Portfolio';
import Contact from './Components/BodyComponents/Contact';
import Footer from './Components/BodyComponents/Footer';
import ClipLoader from "react-spinners/HashLoader";
import { css } from "@emotion/react";


const override = css`
  display: block;
  margin: 0 auto;
  height: 100vh`;


function App() {

  let [loading, setLoading] = useState(true);

  useEffect(() => {
    setLoading(true)
    setTimeout(() => {
      setLoading(false)
    }, 8000);
    
  }, [])
  

  return (
    <div>
    {
      loading ? 
      <ClipLoader color={"#0097a7"} loading={loading} css={override} size={70} />

      : 
<>
      <Header />
    <About />
   <Portfolio />
   <Contact />
  <Footer />
  </>
    }
 
   </div>
  );
}

export default App;
